<template>
	<div class="iswitch" :class="{close: value == conditionClose}" @click="onClick">
		<div class="block" :class="{open: value == conditionOpen}"></div>
		<div class="text">
			<div class="option" :class="{selected: value == conditionOpen}">{{textOpen}}</div>
			<div class="option" :class="{selected: value == conditionClose}">{{textClose}}</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			textOpen: {
				type: String,
				default: '开启'
			},
			textClose: {
				type: String,
				default: '关闭'
			},
			conditionOpen: {
				default: 1
			},
			conditionClose: {
				default: 0
			},
			value: 0,
			keys: {
				type: String,
				default: ''
			},
			type: {
				type: String,
				default: ''
			},
			extra: {
				type: Object,
				default() {
					return {}
				}
			}
		},
		methods: {
			onClick() {
				let value = '';
				if(this.value == this.conditionOpen) {
					value = this.conditionClose;
				} else if(this.value == this.conditionClose) {
					value = this.conditionOpen;
				}
				var data = {
					keys: this.keys,
					value: value,
					type: this.type
				}
				data = Object.assign(data, this.extra);
				this.$emit('change', data);
			}
		},
	}
</script>

<style>
	.iswitch{
		display: inline-block;
		position: relative;
		background-color: #33aafc;
		padding: .5%;
		color: #fff;
		min-width: 80px;
		height: 30px;
		border-radius: 5px;
		border: 1px solid #33aafc;
		transition: all .5s;
	}
	.iswitch.close{
		background-color: #8F8F8F;
		border-color: #8F8F8F;
	}
	.iswitch .block{
		width: 50%;
		background-color: #fff;
		position: absolute;
		top: 0px;
		left: 0px;
		bottom: 0px;
		z-index: 1;
		border-radius: 5px;
		transition: all .5s;
	}
	.iswitch .block.open{
		transform: translateX(100%);
	}
	.iswitch .text{
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 2;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	.iswitch .text .option{
		width: 50%;
		text-align: center;
		color: #7a7a7a;
		transition: all .5s;
	}
	.iswitch .text .option.selected{
		color: #fff;
	}
</style>


