import util from '@/config/util.js'
import router from '@/router'

function orderStatusChangeTips(type) {
	var types = {
		handle: {
			tip: '确定接单吗',
			success_status: 2
		},
		notify_deliveryer_collect: {
			tip: '确定通知配送员抢单吗',
			success_status: 3
		},
		direct_deliveryer: {
			tip: '',
			success_status: 3
		},
		end: {
			tip: '确定完成订单吗',
			success_status: 5
		},
		cancel: {
			tip: '确定取消订单吗',
			success_status: 6
		},
		cancel_refund: {
			tip: '确定取消订单并退款吗',
			success_status: 6
		},
		delivery_ing: {
			tip: '确定设置为配送中吗',
			success_status: 4
		},
		reply: {
			tip: '',
			success_status: 0
		},
		pay_status: {
			tip: '确定设为已支付吗',
			success_status: 0
		}
	}
	return types[type];
};

function changeOrderStatus(options) {
	var order = options.order;
	var type = options.type;
	if(type == 'print') {
		var params = {
			url: 'manage/order/takeout/print',
			data: {id: order.id},
			confirm: '确定打印订单吗',
			success: function(message) {
				util.$toast(message);
				if(options.from == 'detail' && success_status) {
					options.vue.order.print_nums++;
					options.vue.order = Object.assign({}, options.vue.order);
				} else {
					options.vue.records.data[options.index].print_nums++;
				}
			}
		};
		util.jspost(params);
		return;
	}
	var typeInfo = orderStatusChangeTips(type);
	var tip = typeInfo.tip;
	if(type == 'handle' && order.is_reserve == 1) {
		tip = order.handle_tip;
	}
	var success_status = typeInfo.success_status;
	if(type == 'cancel' || type == 'cancel_refund' || type == 'direct_deliveryer' || type == 'reply') {
		router.push(util.getUrl({path: '/pages/order/op', query: {order_id: order.id, type: type, tip: tip, order_status: order.status}}));
	} else {
		var params = {
			url: 'manage/order/takeout/status',
			data: {
				id: order.id,
				type: type
			},
			confirm: tip,
			success: function(message) {
				util.$toast(message);
				if(options.from == 'detail' && success_status) {
					options.vue.order.status = success_status;
					options.vue.order = Object.assign({}, options.vue.order);
				} else {
					if(type == 'notify_deliveryer_collect' && options.vue.filter.items.status == 3) {
						return;
					}
					var types = ['handle', 'end', 'notify_deliveryer_collect', 'delivery_ing'];
					if(types.indexOf(type) != -1 && options.index >= 0) {
						if(options.from == 'search') {
							options.vue.records.data[options.index].status = success_status;
						} else {
							options.vue.records.data.splice(options.index, 1);
						}
					}
				}
			}
		};
		util.jspost(params);
	}
};

function changeTangshiOrderStatus(options) {
	var order = options.order;
	var type = options.type;
	var typeInfo = orderStatusChangeTips(type);
	var tip = typeInfo.tip;
	var success_status = typeInfo.success_status;
	var url = 'manage/order/takeout/status';
	if(type == 'cancel' || type == 'cancel_refund') {
		url = 'manage/order/tangshi/cancel';
	} else if(type == 'pay_status') {
		url = 'manage/order/tangshi/pay_status';
	}
	var params = {
		url: url,
		data: {
			id: order.id,
			type: type
		},
		confirm: tip,
		success: function(message) {
			util.$toast(message);
			if(options.from == 'detail' && success_status) {
				options.vue.order.status = success_status;
				options.vue.order = Object.assign({}, options.vue.order);
			} else {
				if(type == 'pay_status') {
					options.vue.records.data[options.index].is_pay = 1;
					return;
				}
				var types = ['handle', 'end', 'cancel', 'cancel_refund'];
				if(types.indexOf(type) != -1 && options.index >= 0) {
					options.vue.records.data.splice(options.index, 1);
				}
			}
		}
	};
	util.jspost(params);
}

function pushUUpaotui(options) {

};

function confirmOrderByCode(options) {
	var code = options.code;
	var sid = options.sid;
	var params = {
		url: 'manage/order/takeout/confirm',
		data: {
			code: code,
			sid: sid
		},
		confirm: options.confirm,
		success: function(message) {
			if(options.from == 'detail') {
				util.$toast(message, 'refresh', 1500);
			} else {
				options.vue.code = '';
				util.$toast(message);
			}
		}
	};
	util.jspost(params);
	return;
}

export {
	orderStatusChangeTips,
	changeOrderStatus,
	changeTangshiOrderStatus,
	confirmOrderByCode
}



