<template>
	<div id="public-header">
		<van-nav-bar :title="title" :left-arrow="back" @click-left="onClickLeft" :style="{background: headerstyle.background, color: headerstyle.color }">
			<div slot="right">
				<slot name="right"></slot>
			</div>
		</van-nav-bar>
	</div>
</template>

<script>
	export default {
		name: 'PublicHeader',
		props:{
			title: String,
		},
		data() {
			return {
				back: false,
				headerstyle: {
					background: '#33aafc',
					color: '#fff'
				}
			}
		},

		methods: {
			onClickLeft() {
				if(!this.back) {
					return;
				}
				this.$router.back(-1);
			},
		},
		mounted() {
			if(window.history.length > 1) {
				this.back = true;
			}
		}
	}
</script>

<style>
	#public-header{
		position: fixed;
		top: 0;
		right: 0;
		left: 0;
		z-index: 100;
	}
	#public-header>div{
		background-color: #33aafc;
		color: #fff;
		font-size: 16px;
	}
	#public-header .van-icon{
		font-weight: bold;
		color: #fff;
	}
	#public-header .van-hairline--bottom::after {
		border-bottom-width: 0px;
	}
	#public-header .van-nav-bar__title{
		color: #fff!important;
	}
</style>



